import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import index from '@/components/index'
import shopCar from '@/components/shopCar'
import order from '@/components/order'
import more from '@/components/more/more'
import about from '@/components/more/about'
import address from '@/components/more/address'
import addAddress from '@/components/more/addAddress'
import search from '@/components/search'
import pay from '@/components/pay'
import bookInfo from '@/components/bookInfo'
import list from '@/components/sorts/list'
import people from '@/components/people/people'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/home',
      name: 'home',
      // redirect:"/index",
      component:home,
        children:[,
        {
          path:'/shopCar',
          name:"shopCar",
          component:shopCar
        },
        {
          path:'/order',
          name:"order",
          component:order
        },{
          path:'/more',
          name:"more",
          component:more
        },
        {
          path:'/index',
          name:"index",
          component:index
        }
        ]
    },
    {
      path:'/about',
      name:"about",
      component:about
    },{
      path:'/search',
      name:"search",
      component:search
    },{
      path:'/bookInfo/:id',
      name:"bookInfo",
      component:bookInfo,
      meta:{
        keepAlive: true
      }
    },{
      path:'/pay',
      name:"pay",
      component:pay
    },{
      path:'/list',
      name:"list",
      component:list
    },{
      path:'/address',
      name:"address",
      component:address
    },{
      path:'/addAddress',
      name:"addAddress",
      component:addAddress
    },{
      path:'/people',
      name:"people",
      component:people
    }
  ]
})



