<template>
<div>
  <mt-navbar v-model="selected" :style="{background:all.window.navigationBarBackgroundColor}">
    <mt-tab-item id="1" style="color:white;"><i class="iconfont" style="margin-right: 0.1rem">&#xe651;</i>待发货</mt-tab-item>
    <mt-tab-item id="2" style="color:white;"><i class="iconfont" style="margin-right: 0.1rem">&#xe616;</i>已发货</mt-tab-item>
    <mt-tab-item id="3" style="color:white;"><i class="iconfont" style="margin-right: 0.1rem">&#xe6f8;</i>已退款</mt-tab-item>
  </mt-navbar>
  <!-- tab-container -->
  <mt-tab-container v-model="selected">
    <mt-tab-container-item id="1">
      <div class="good">
        <img src="../assets/bander.png" alt="">
        <p class="p">预览模式下不支持订单查看</p>
      </div>
    </mt-tab-container-item>
    <mt-tab-container-item id="2">
      <div class="gooded">
        <i class="iconfont icon"  style="font-size: 0.3rem">&#xe602;</i>
        <p class="p">预览模式下不支持订单查看</p>
      </div>
    </mt-tab-container-item>
    <mt-tab-container-item id="3">
      <div class="refund">
        <i class="iconfont icon"  style="font-size: 0.3rem">&#xe602;</i>
        <p class="p">预览模式下不支持订单查看</p>
      </div>
    </mt-tab-container-item>
  </mt-tab-container>
</div>
</template>
<script>
export default{
    data(){
        return {
          selected:'1',
          color:"blue"
        }
    },
  computed:{
    all(){
      return this.$store.getters.getAll;
    }
  },
}
</script>
<style scoped>
.good{
  height: calc(100vh - 150px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.gooded{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 150px);
}
.refund{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 150px);
}
  .icon{
    font-size: 0.5rem;
    color: darkgrey;
  }
  .p{
    margin-top: 0.2rem;
    font-size: 0.25rem;
    color: darkgrey;
  }
</style>



