<template>
  <div class="pay">
    <mt-header title="订单" :style="{background:all.window.navigationBarBackgroundColor}">
      <router-link to="/bookInfo/0" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
    <div class="pay_info">
      <div class="goodInfo">
        <div class="left">
          <img :src="bookInfo.info.pic" alt="" style="width: 100%;max-height: 100%">
        </div>
        <div class="center">
            <p>{{bookInfo.info.service_name}}</p>
            <span>1套</span>
            <span>¥ {{bookInfo.info.service_price}}</span>
        </div>
      </div>
      <div class="order_time">
        <p>时间: <span>{{bookInfo.date}}</span><span>{{bookInfo.time}}:00</span></p>
        <p>服务人员: <span>{{people.name}}</span></p>
      </div>
    </div>
    <p class="title">客户信息</p>
    <div>
      <mt-field label="姓名"   placeholder="请输入真实姓名"></mt-field>
      <mt-field label="电话"  type="number"  placeholder="请输入手机号码"></mt-field>
    </div>
    <p class="title">留言</p>
    <div style="display: flex">
      <textarea id=""  rows="10" placeholder="有其他需求请填写" class="input"></textarea>
    </div>
    <footer class="btn">
      <mt-button  size="large" style="font-size: 15px;color: white;" :style="{background:all.window.navigationBarBackgroundColor}"  @click="submitOrder">提交订单</mt-button>
    </footer>
  </div>
</template>
<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return {
        color:'blue',
        sheetVisible:false,
      }
    },
    computed:{
      all(){
        return this.$store.getters.getAll;
      },
      people(){
        return this.$store.getters.getPeople;
      },
      bookInfo(){
          return this.$store.getters.getBookInfo;
      }
    },
    methods: {
      submitOrder(){
        MessageBox('提示', '预览模式暂不支持,请手机预览');
      },
      share(){
        this.sheetVisible = true;
      },
    }
  }
</script>
<style scoped>
  .goodInfo{
    height: 1.4rem;
    display: flex;
    align-items: center;
}
  .order_time{
    padding: 0.2rem;
  }
  .left{
    padding: 5px;
    border: 1px solid darkgrey;
    flex:1;
    margin: 0 0.2rem;
    text-align: center;
  }
  .center{
    flex: 4;
  }
  .title{
    padding: 0.2rem;
    background: #f7f7f7;
  }
  .input{
    padding: 0.15rem;
    text-indent: 0.1rem;
    flex:1;
  }
  .btn{
    width: 100vw;
    padding: 2px 0;
    position: fixed;
    bottom:0;
    color: white;
  }
</style>



