<template>
  <div>
    <header-bar></header-bar>
    <p class="title">联系方式</p>
    <div>
      <mt-field label="姓名"   placeholder="请输入真实姓名"></mt-field>
      <mt-field label="电话"  type="number"  placeholder="请输入手机号码"></mt-field>
      <mt-field label="电子邮箱"  type="email" placeholder="用于接受订单状态"></mt-field>
    </div>
    <p class="title">收货地址</p>
    <div>
      <mt-field label="姓名"   placeholder="请输入真实姓名"></mt-field>
      <mt-field label="详细收货地址"  type="number"  placeholder="例如街道、门牌"></mt-field>
      <mt-field label="邮编" placeholder="选填"></mt-field>
    </div>
    <footer class="btn">
      <mt-button  size="large" style="font-size: 15px;color: white;}" :style="{background: all.window.navigationBarBackgroundColor}">保存</mt-button>
    </footer>
  </div>
</template>
<script>
  import headerBar from '../header.vue'
export default{
    data(){
        return {

        }
    },
  computed:{
    all(){
      return this.$store.getters.getAll;
    }
  },
  components:{
    headerBar
  }
}
</script>
<style scoped>
  .title{
    padding: 0.2rem;
    background: #f7f7f7;
  }
  .btn{
    width: 90vw;
    padding: 2px 0;
    margin: 2vh 5vw;
  }
</style>



