<template>
  <div class="search">
    <mt-header title="搜索" :style="{background: all.window.navigationBarBackgroundColor}">
      <router-link to="/index" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <header class="header" :style="{background:all.window.navigationBarBackgroundColor}">
      <p>
        <i class="iconfont icon" style="font-size: 0.3rem">&#xe603;</i>
        <input type="text"  placeholder="搜索商品" class="input">
      </p>
    </header>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
  </div>
</template>
<script>
export default{
    data(){
      return {
        color:"blue",
        sheetVisible:false
      }
    },
  computed:{
    all(){
      return this.$store.getters.getAll;
    }
  },
  methods:{
        share(){
            this.sheetVisible=true
        }
  }
}
</script>
<style scoped>
  .header{
    height: 50px;
    background: blue;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  header>p{
    width: 90vw;
    padding: 0.1rem;
    background-color: #f6f6f6;
    border-radius: 0.1rem;
    margin-bottom: 5px;
  }
  .input{
    display: inline-block;
    height: 0.4rem;
    padding: 0 0.05rem;
    width: 90%;
    background-color: #f6f6f6;
  }
</style>



