<template>
  <div class="list">
    <mt-header title="2017秋季潮流款" :style="{background:color}">
      <router-link to="/index" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
      <div class="all">
        <div @click="seeInfo(key)" class="info" v-for="(item,key) in lists">
          <img :src="item.img" alt="" >
          <p style="margin-top: 0.25rem">{{item.name}}</p>
          <p>¥{{item.price}}</p>
        </div>
      </div>
  </div>
</template>
<script>
export default{
    data(){
        return {
          color:"blue",
          sheetVisible:false,
          lists:[
            {
              img:require('../../assets/04.png'),
              name:'潮流短靴',
              price:"86.86"
            }
          ]
        }
    },
  computed:{
    all(){
      return this.$store.getters.getAll;
    }
  },
  methods:{
    share(){
      this.sheetVisible=true;
    },
    seeInfo(key){
      let  url='/bookInfo/'+key;
      this.$router.push(url);
    }
  }
}
</script>
<style scoped>
  .list{
    position: fixed;
    top:0;
    bottom: 0;
    left:0;
    right: 0;
    background: #f7f7f7;
  }
  .all{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  .info{
    margin: 0.25rem;
    width: 42vw;
    padding: 0.2rem 0;
    text-align: center;
    background: white;
  }
  .info>p{
    text-align: left;
    margin-left: 0.35rem;
  }
</style>



