<template>
<div class="more">
  <header>
    <img src="../../assets/logo.png" alt="">
    <p>测试账号</p>
  </header>
  <mt-cell title="官方网站" is-link @click.native="see"></mt-cell>
  <mt-cell title="关于我们" is-link @click.native="about"></mt-cell>
  <mt-cell title="客服电话" :value="tel"></mt-cell>
  <mt-cell title="门店地址" :value="location"></mt-cell>
</div>
</template>
<script>
  import { MessageBox } from 'mint-ui';
export default{
    data(){
        return {
          pic:"",
          tel:'15537839367',
          location:'河南郑州'
        }
    },
  computed:{
    all(){
      return this.$store.getters.getAll;
    }
  },
  methods:{
    see(){
      MessageBox('欢迎访问我们的网站',"www.xiguakeji.cc");
    },
    about(){
        this.$router.push('/about')
    }
  },
  created(){
//        this.pic=
  }
}
</script>
<style scoped>
  header{
    height: 30vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  header>img{
    width:1rem;
    max-height: 100%;
    border-radius: 50%;
    margin-bottom: 0.1rem;
  }
  header>p{
    font-size: 0.25rem;
    font-weight: 600;
  }
  .address{
    padding: 0.1rem 0;
  }
</style>



