<template>
    <div class="slide">
      <div   v-for="(item,key) in lists">
        <p class="title">2017秋季潮流款</p>
        <div class="info" @click="seeInfo(key)">
          <img :src="item.img" alt="" >
          <p style="margin-top: 0.25rem">{{item.name}}</p>
          <p>¥{{item.price}}</p>
        </div>
      </div>
    </div>
</template>
<script>
  export default{
    data(){
      return {
        lists:[
          {
            img:require('../../assets/01.png'),
            name:'棒球衣',
            price:"66.00"
          },
          {
            img:require('../../assets/02.png'),
            name:'潮流男士衬衣',
            price:"88.88"
          },
          {
            img:require('../../assets/03.png'),
            name:'潮流短靴',
            price:"111.11"
          },
          {
            img:require('../../assets/04.png'),
            name:'迷彩男裤',
            price:"86.86"
          }
        ]
      }
    },
    methods:{
      share(){
        this.sheetVisible=true;
      },
      seeInfo(key){
        let  url='/goodsInfo/'+key;
        this.$router.push(url);
      }
    }
  }
</script>
<style scoped>
  .title{
    margin: 0.2rem 0.2rem;
  }
  .slide{
    /*width: 100vw;*/
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
  .info{
    margin: 0.25rem;
    width: 39vw;
    padding: 0.2rem 0;
    text-align: center;
    background: white;
  }
  .info>p{
    text-align: left;
    margin-left: 0.35rem;
  }
</style>



