<template>
<div class="shop">
  <div class="shop_null" v-if="goods.length===0">
    <i class="iconfont icon" style="font-size: 0.4rem;color: darkgrey">&#xe600;</i>
    <p>空购物车</p>
    <p>快去添加商品吧</p>
  </div>
  <ul>
    <li class="list" v-for="(item,key) in goods">
      <div class="delete" @click="deleted(key)">
        <img src="../assets/delete.png" alt="">
      </div>
      <div class="goods">
        <img :src="item.pic" alt="" style="width: 100px;max-height: 100%">
      </div>
      <div class="right">
        <div>{{item.name}}</div>
        <div class="num">
          <p>{{item.num}}件</p>
          <p style="color: rgba(235,64,64,1)">¥{{item.num*item.price}}</p>
        </div>
      </div>
    </li>
  </ul>
  <div class="btn">
    <div class="btn_left">
      <p>总计（不含运费）：</p>
    </div>
    <div class="btn_right" :style="{background:all.window.navigationBarBackgroundColor}" @click="pay">
      结算
    </div>
  </div>
</div>
</template>
<script>
export default {
  data(){
      return {
          items:[1,2,3]
      }
  },
  computed:{
    goods(){
      return this.$store.getters.getGoods;
    },
    all(){
      return this.$store.getters.getAll;
    }
  },
  methods:{
    pay(){
      this.$router.push('pay');
      },
    deleted(index){
       this.goods.splice(index,1)
    }
  }
}
</script>
<style scoped>
.shop{
height: 100%;
}
.shop_null{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.shop_null>p{
  margin-top: 0.1rem;
  text-align: center;
  font-size: 0.25rem;
  color: darkgrey;
}
.list{
  display: flex;
  padding: 0.2rem;
  justify-content: center;
  align-items: center;
  background: white;
  margin-bottom: 0.1rem;
}
.delete{
  padding: 0 0.2rem;
}
.goods{
  padding: 0 0.4rem;
  text-align: center;
}
  .right{
    flex:1;
    font-size: 14px;
  }
  .right>div{
    padding: 0 0.2rem;
    margin: 0.1rem 0;
  }
  .num{
    display: flex;
    justify-content: space-between;
  }
  .num>p{
    font-size: 14px;
  }
  .shop{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .btn{
    display: flex;
    justify-content: space-between;
    height:44px;
    background:rgba(255,255,255,1);
  }
  .btn_left{
    height:44px;
    flex:1;
    line-height: 44px;
    margin-left:0.2rem;
  }
  .btn_right{
    font-size: 14px;
    line-height: 44px;
    text-align: center;
    color: white;
    width:2rem;
    height:44px;
    background:rgba(29,29,29,1);
  }
</style>



