<template>
  <div class="address">
    <header-bar></header-bar>
    <footer>
      <mt-button size="large" class="btn" :style="{background: all.window.navigationBarBackgroundColor}" @click="addAddress">添加新地址</mt-button>
    </footer>
  </div>
</template>
<script>
  import headerBar from '../header.vue'
export default{
    data(){
        return {
          color:"blue"
        }
    },
  computed:{
    all(){
      return this.$store.getters.getAll;
    }
  },
  methods:{
    addAddress(){
        this.$router.push('/addAddress')
    }
  },
  components:{
    headerBar
  }
}
</script>
<style scoped>
  footer{
    width: 100vw;
    position: fixed;
    bottom: 0;
  }
  .btn{
    font-size: 15px;
    color: white;
  }
</style>



