<template>
    <div>
      <div class="content">
          <div class="carousel" v-if="getCarousel.length!==0">
              <mt-swipe :auto="4000">
                <mt-swipe-item v-for="(item,key) in getCarousel" :key="key"><img :src="item.pic" alt="" style="width: 100vw;max-height: 100%"></mt-swipe-item>
                <!--<mt-swipe-item><img src="../assets/spanner.png" alt="" style="max-width: 100%"></mt-swipe-item>-->
                <!--<mt-swipe-item><img src="../assets/oh.png" alt="" style="max-width: 100%"></mt-swipe-item>-->
              </mt-swipe>
            </div>
            <div>
              <mt-navbar v-model="selected">
                <mt-tab-item id="1">商品</mt-tab-item>
                <mt-tab-item id="2">分类</mt-tab-item>
              </mt-navbar>
              <!-- tab-container -->
              <mt-tab-container v-model="selected">
                <mt-tab-container-item id="1">
                  <boxGoods></boxGoods>
                </mt-tab-container-item>
                <mt-tab-container-item id="2">
                  <listSort></listSort>
                </mt-tab-container-item>
              </mt-tab-container>
            </div>
            <div v-if="false">
              <slide></slide>
            </div>
      </div>
    </div>
</template>
<script>
  import listSort from './sorts/listSort.vue'
  import boxGoods from './goods/goods.vue'
  import slide from './goods/slide.vue'
export default{
    data(){
        return {
          selected:"1",
        }
    },
    computed:{
      getCarousel(){
        return this.$store.getters.getCarousel;
      },
      all(){
        return this.$store.getters.getAll;
      }
    },
    methods:{
    },
  components:{
    listSort,
    boxGoods,
    slide
  },
  created(){
  }
}
</script>
<style scoped>
  header{
    top:50px;
    height: 50px;
    background: blue;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  header>p{
    width: 90vw;
    padding: 0.12rem 0.1rem;
    background-color: #f6f6f6;
    border-radius: 0.1rem;
    margin-bottom: 5px;
  }
  .content{
    height: calc(100vh - 100px);
    overflow-y: scroll;
    -webkit-overflow-scrolling : touch;
  }
  .content::-webkit-scrollbar {
    display: none;
  }
  .carousel{
    height: 40vw;
  }
  .all{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  .info{
    margin: 0.25rem;
    width: 42vw;
    text-align: center;
  }
  .info>p{
    text-align: left;
    margin-left: 0.35rem;
  }
</style>



