<template>
  <div class="hello">
    <mt-header :title="title" :style="{backgroundColor:all.window.navigationBarBackgroundColor}">
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <div class="content">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
    <footer>
        <div v-for="(item,key) in lists" @click="add(key)">
          <p>
            <i class="iconfont" :style="{color:item.color}" v-if="key===0" style="font-size: 0.4rem">&#xe65d;</i>
            <i class="iconfont icon" :style="{color:item.color}" v-if="key===1" style="font-size: 0.3rem">&#xe637;</i>
            <i class="iconfont icon" :style="{color:item.color}" v-if="key===2" style="font-size: 0.3rem">&#xe643;</i>
          </p>
          <p>{{item.name}}</p>
        </div>
    </footer>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      data:{},
      sheetVisible:false,
      lists:[
          {
              name:'预约',
              color:"#222222",
          },{
              name:'订单',
              color:"#222222",
          },{
              name:'我的',
              color:"#222222",
          }
          ],
      color:"blue",
      header:"blue",
      title:"预约"
    }
  },
  computed:{
    all(){
        return this.$store.getters.getAll;
    }
  },
  methods:{
    share(){
      this.sheetVisible = true;
    },
//    取消颜色选择
    selected(){
      this.lists.forEach(item=>{
          item.color = this.all.tabBar.backgroundColor;
      })
    },
    add(key){
      switch(key){
        case 0 :
            this.selected();
            this.$router.push('/index');
            this.title = this.all.window.navigationBarTitleText;
            this.lists[key].color = this.all.tabBar.selectedColor;
            break;
        case 1 :
          this.selected();
          this.$router.push('/order');
          this.title="订单";
          this.lists[key].color = this.all.tabBar.selectedColor;
          break;
        case 2 :
          this.selected();
          this.$router.push('/more');
          this.title="我的";
          this.lists[key].color = this.all.tabBar.selectedColor;
          break;
      }
    },
    select(name){
        switch(name){
          case 'index':
            this.lists[0].color = this.all.tabBar.selectedColor;
            break;
          case 'shopCar':
            this.lists[1].color = this.all.tabBar.selectedColor;
            break;
          case 'order':
            this.lists[2].color = this.all.tabBar.selectedColor;
            break;
          case 'more':
            this.lists[3].color = this.all.tabBar.selectedColor;
            break;
        }
    }
  },
  watch:{
    "$route"(to,from){
      let name=to.name;
    }
  },
  created(){
    let name=this.$route.name;
    this.select(name);
    let url = window.location, urlData=decodeURIComponent(url);
//    let params = (new URL(url)).searchParams;
    let key = urlData.indexOf("data")+5;
    if(key !== 4){
      let data = JSON.parse(urlData.slice(key));
      let that = this;
      window.addEventListener('message',e => {
        let params=e.data;
        if(Object.prototype.toString.call(params)==="[object Array]"){
          if(params[0]["flag"]===undefined) {
            this.$store.dispatch("goodsList", params);
            sessionStorage.setItem('bookGoodsList', JSON.stringify(params));
          }else{
            this.$store.dispatch("sortsList", params);
            sessionStorage.setItem('bookSortsList', JSON.stringify(params));
          }
        }else if(Object.prototype.toString.call(params)==="[object Object]"){
          if("attr" in params){
            Object.assign(data.ext.attr,params["attr"]);
          }
          if("tabBar" in params){
            Object.assign(data.tabBar,params["tabBar"]);
          }
          if("window" in params) {
            Object.assign(data.window, params["window"]);
          }
          if("flag" in params){
            this.$store.commit('setCarouselFlag',params);
          }
          if("link" in params){
            this.$store.dispatch('carousel',params);
          }
        }
        this.lists.forEach(item=>{
          item.color=data.tabBar.backgroundColor;
        });
        this.$store.dispatch('all',data);
        this.add(0);
      });
      this.lists.forEach(item=>{
        item.color=data.tabBar.backgroundColor;
      });
      this.$store.dispatch('all',data);
      this.add(0);
    }
  },
  mounted(){
   sessionStorage.getItem('bookGoodsList') && this.$store.dispatch("goodsList",JSON.parse(sessionStorage.getItem('bookGoodsList')));
   sessionStorage.getItem('bookSortsList')&& this.$store.dispatch("sortsList",JSON.parse(sessionStorage.getItem('bookSortsList')));
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  footer{
    position: fixed;
    bottom: 0;
    width: 100%;
    height:9vh;
    display: flex;
    align-items: center;
  }
  footer>div{
    flex:1;
    height:60px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /*text-align: center;*/
    color: darkgrey;
  }
  .icon{
    display: inline-block;
    padding-top: 0.02rem;
    line-height: 28px;
  }
  .content{
    width: 100vw;
    height: calc(100vh - 100px);
    background-color: #f6f6f6;
  }
</style>



