<template>
  <div class="about">
    <mt-header title="关于我们" :style="{background:all.window.navigationBarBackgroundColor}">
      <router-link to="/more" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
      <div class="about_content">
        <img src="../../assets/logo.png" alt="">
        <p>测试账号</p>
      </div>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
  </div>
</template>
<script>
export default{
    data(){
      return {
        sheetVisible:false,
        color:'blue'
      }
    },
  computed:{
    all(){
      return this.$store.getters.getAll;
    }
  },
  methods:{
    share(){
      this.sheetVisible=true;
    }
  }
}
</script>
<style scoped>
  .about{
    width: 100vw;
    height: 100vh;
    background-color: #f6f6f6;
  }
  .about_content{
    text-align: center;
    margin: 50% 0;
  }
  .about_content>img{
    width: 100px;
    max-height: 100%;
  }
  .about_content>p{
      margin-top: 20px;
      /*font-weight: 600;*/
      font-size:0.25rem ;
  }
</style>



