<template>
  <div class="all">
    <div  class="info" v-for="(item,key) in lists">
      <div class="out" @click="seeInfo(key)">
        <p>{{item.name}}</p>
        <!--<p>2017秋季潮流款</p>-->
      </div>
      <div class="enter">
        <!--<img :src="item.img" alt="" >-->
      </div>
    </div>
  </div>
</template>
<script>
export default{
    data(){
        return {
          layout:"row",
          lists:[
            {
              img:require('../../assets/01.png'),
              name:'棒球衣',
              price:"66.00"
            },
            {
              img:require('../../assets/02.png'),
              name:'潮流男士衬衣',
              price:"88.88"
            },
            {
              img:require('../../assets/03.png'),
              name:'潮流短靴',
              price:"111.11"
            },
            {
              img:require('../../assets/04.png'),
              name:'迷彩男裤',
              price:"86.86"
            }
          ]
        }
    },
  computed:{
    sortsList(){
      return this.$store.getters.getSortsList;
    },
    all(){
      let data=this.$store.getters.getAll;
//      if(data.ext.attr.layout==="0"){
////            方块
//        this.layout="row"
//      }else if(data.ext.attr.layout==="1"){
////            列表
//       this.layout="column"
//      }
      return this.layout;
    }
  },
  methods:{
    seeInfo(key){

        this.$router.push('/list')
    }
  }
}
</script>
<style scoped>
  .all{
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    /*flex-direction: column;*/
  }
  .info{
    margin: 0.25rem;
    width: 39vw;
  }
  .out{
    width: 42vw;
    height: 20vh;
    padding: 0.1rem;
    position:absolute;
    z-index:3;
   background:rgba(37,37,37,0.5);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .out>p{
    color: white;
    padding: 0.1rem;
  }
  .out>p:nth-child(1){
    background:rgba(55,55,55,1);
    opacity:0.65;
  }
  .enter{
    width: 42vw;
    height: 20vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
</style>



