import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);
export default new Vuex.Store({
  state:{
  //save shopCar goods
    goods:[],
  //  样式保存;
    all:{},
  // 商品列表
    goodsList:[],
  //  分类列表
    sortsList:[],
  //  轮播图
    carousel:[],
    carouselFlag:0,
  //  获取预约人员
    people:{},
    // 标识
    flag:false,
  //  获取预约时间和商品详情
    bookInfo:{}
  },
  getters:{
    //
    getBookInfo:state => state.bookInfo,
    getFlag(state){
      return state.flag;
    },
    getPeople(state){
      return state.people;
    },
  //  获取轮播列表
    getCarousel(state){
      return state.carousel;
    },
  //    获取分类列表
    getSortsList(state){
      return state.sortsList;
    },
    //获取商品列表
    getGoodsList(state){
      return state.goodsList;
    },
    getGoods(state){
      return state.goods
    },
    getAll(state){
      return state.all;
    }
  },
  mutations:{
    setBookInfo: (state,params) => state.bookInfo = params,
    setFlag(state,params){
       return state.flag=params;
    },
    setPeople(state,params){
      return state.people=params;
    },
    setCarouselFlag(state,params){
      return state.carouselFlag=params;
    },
    //设置轮播列表
    setCarousel(state,params){
      if(state.carouselFlag.flag===1){
        return state.carousel.push(params);
      }else if(state.carouselFlag.flag===2){
        return state.carousel.splice(state.carouselFlag.id,1,params);
      }
    },
    //设置商品列表
    setSortsList(state,params){
      return state.sortsList=params;
    },
    //设置商品列表
    setGoodsList(state,params){
      return state.goodsList=params;
    },
    setGoods(state,params){
    return state.goods.push(params)
    },
    setAll(state,params){
     return state.all=params;
    }
  },
  actions:{
    bookInfo: ({commit},params) => commit('setBookInfo',params),
    flag({commit},params){
      commit('setFlag',params);
    },
    people({commit},params){
      commit("setPeople",params)
    },
    carousel({commit},params){
      commit('setCarousel',params);
    },
    sortsList({commit},params){
    commit('setSortsList',params);
    },
    goodsList({commit},params){
     commit("setGoodsList",params);
    },
    goods({commit},params){
      commit('setGoods',params);
    },
    all({commit},params){
      commit('setAll',params);
    }
  }
})



