<template>
  <div class="address">
    <mt-header  :style="{background: all.window.navigationBarBackgroundColor}" title="管理收货地址">
      <router-link slot="left" to="">
        <mt-button icon="back" @click.native="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        color:'blue',
        sheetVisible:false
      }
    },
    computed:{
      all(){
        return this.$store.getters.getAll;
      }
    },
    methods:{
      share(){
          this.sheetVisible=true;
      },
      back(){
          this.$router.go(-1);
      }
    }
  }
</script>
<style scoped>

</style>




