<template>
  <div>
    <mt-header  :style="{background:all.window.navigationBarBackgroundColor}" title="服务人员">
      <router-link to="/bookInfo/0" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
    <div class="infos">
      <ul>
        <li class="people" v-for="(item,key) in all.tabBar.list" @click="selected(key)">
          <img :src="item.pic" alt="" style="width: 60px;height: 60px">
          <div class="info">
            <p>{{item.name}}</p>
            <p>{{item.desc}}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        color:'blue',
        sheetVisible:false,
        items:[
          {
            pic:require('../../assets/01.png'),
            name:"汪华",
            desc:"高级剪发师"
          }
        ]
      }
    },
    computed:{
      all(){
        return this.$store.getters.getAll;
      }
    },
    methods:{
      share(){
        this.sheetVisible=true
      },
      selected(key){
        this.$store.dispatch('people',this.all.tabBar.list[key]);
        this.$router.push(`/bookInfo/${key}`);
      }
    },
    watch:{

    },
    created(){

    },
    mounted(){

    }
  }
</script>
<style scoped>
.people{
  padding: 0.2rem;
  display: flex;
  background-color: white;
  margin-bottom: 0.05rem;
}
.infos{
  height: calc(100vh - 41px);
  overflow-y: auto;
  background-color: gainsboro;
}
.infos::-webkit-scrollbar{
  display: none;
}
  .info{
    margin-left: 0.2rem;
  }
</style>



